<template>
    <div id="wrapper">
        <navbar id="nav" class="no-print animate__animated animate__fadeInLeft"  style="display: none;"/>
        <div class="d-flex flex-column animate__animated animate__fadeInRight" id="content-wrapper" style="display: none !important;">
            <div id="content">
                <header-btn class="no-print"/>
                <div class="container-fluid text-dark">
                    <div class="row px-1 py-3 mb-1 no-print">
                        <label class="mt-1 col-xl-1 mb-2"> From: </label>
                        <input class="form-control col-xl-2 col-md-4 mb-2" v-model="from" type="date">

                        <label class="mt-1 col-xl-1 mb-2"> To: </label>
                        <input type="date" class="form-control col-xl-2 col-md-4 mb-2" v-model="to">
                        
                        <label class="col-sm-1 mt-1"> Staffs: </label>
                        <select v-model="staff_id" class="form-control col-sm-3 mb-2">
                            <option value=""></option>
                            <option v-for="staff in staffs" :key="staff.st_id" v-bind:value="staff.st_id"> {{ staff.staff_name }} </option>
                        </select>

                        <button class="btn btn-success col-xl-2 col-md-4 mx-0 mb-2" v-on:click="search()"> Search <i class="fa fa-search"></i> </button><hr>
                    </div>
                    <hr class="no-print">
                    <div class="row">
                        <div class="col-4" style="color: black;">
                            <h2> Ideal Power Company </h2>
                            <p> For Contracting Electrical, Mechanical,                                                                                                             General Trading, Renewable Energy
                             &#38; Clean Energy / LTD
                            </p>
                        </div>
                        <div class="col-4" style="color: black;">
                            <img src="../assets/Ideal power logo.png" width="70%">
                        </div>
                        <div class="col-4" style="color: black;">
                            <h2> Ideal Power Company </h2>
                            <p> For Contracting Electrical, Mechanical,                                                                                                             General Trading, Renewable Energy
                             &#38; Clean Energy / LTD
                            </p>
                        </div>
                    </div>
                    <div class="table-responsive">
                        <table class="table table-bordered table-striped text-center" style="page-break-inside: always; font-size: 12pt !important;">
                            <thead>
                                <th> Expense </th>
                                <th> Staff </th>
                                <th> Quantity </th>
                                <th> Price </th>
                                <th> Total </th>
                                <th> Date </th>
                            </thead>
                            <tbody>
                                <tr v-for="item in expenses" :key="item.staff_expense_id">
                                    <td> {{ item.expense_text }} </td>
                                    <td> {{ item.staff_name }} </td>
                                    <td> {{ item.qty }} </td>
                                    <td> {{ (item.price).toLocaleString() }} </td>
                                    <td> {{ (item.total).toLocaleString() }} </td>
                                    <td> {{ new Date(item.expense_date).toISOString().split('T')[0] }} </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
import auth from '../auth';
export default {
    name: "staff_expenses_report",
    data() {
        return {
            staff_id: null,
            staffs: [],
            expenses: [],
            month: new Date().getMonth() + 1,
            year: new Date().getFullYear(),
            from: new Date().toISOString().split('T')[0],
            to: new Date().toISOString().split('T')[0],
            employees: [],
        }
    },
    beforeCreate() {
        auth("staff_expenses_report");
    },
    created() {
        this.$http.post('/staff/getData').then(({data})=>{
            this.staffs = data.filter(obj => obj.show_staff == '1');
        })

    },
    methods: {
        search() {
            if(this.from && this.to && this.staff_id) {
                this.$http.get('/staff_expenses/getDataByStaff/' + this.staff_id + '/' + this.from + '/' + this.to).then(({data}) => {
                    this.expenses = data;
                })
            } else {
                this.expenses = [];
            }
        }
    },
    watch: {
        staff_id() {
            this.search();
        },
        from() {
            this.search();
        },
        to() {
            this.search();
        },
    }
}
</script>

<style scoped>
    .rtl {
        text-align: right !important;
    }
    tbody td {
        padding: .5rem;
    }
    .para {
        background-color: rgb(81, 224, 81) !important;
        color: black !important;
    }
    .para-total {
        background-color: rgb(68, 171, 231) !important;
        color: black !important;
    }
    th, td {
        font-size: 12pt;
        color: black;
        font-weight: 500;
    }
    @media print {
        .no-print, #nav {
            display: none;
        }
        *:not(.para, .para-total) {
            background-color: white ;
            
        }
        .para {
            background-color: rgb(81, 224, 81) !important;
            color: black !important;
        }
        .para-total {
            background-color: rgb(68, 171, 231) !important;
            color: black !important;
        }
        table {
            font-size: 9pt !important;
        }
        @page {
            size: landscape;
        }
    }
</style>